<template>
    <v-container class="py-6" fluid>
        <v-row>
            <v-col cols="4">
                <v-select
                    v-model="quickDateRange"
                    :color="getAppColors.primary"
                    item-color="green"
                    :items="getQuickDateRangeChoices"
                    class="fit-content"
                    single-line
                    hide-details
                    return-object
                    solo
                    @change="initialize"
                />
            </v-col>
        </v-row>
        <v-row v-show="alert">
            <v-col cols="12">
                <v-alert
                    v-model="alert"
                    :type="alertType"
                    border="left"
                    close-text="Close Alert"
                    dismissible
                    transition="scale-transition"
                >
                    {{ alertMessage }}
                </v-alert>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="12" sm="4">
                <stat-card
                    :stat-value="totalVolume"
                    card-title="Volume"
                    card-sub-title="Total dollar amount processed"
                    :loading="loading.stats"
                />
            </v-col>
            <v-col cols="12" sm="4">
                <stat-card
                    :stat-value="totalCount"
                    card-title="Transactions"
                    card-sub-title="Total number of transactions processed"
                    :loading="loading.stats"
                />
            </v-col>
            <v-col cols="12" sm="4">
                <stat-card
                    :stat-value="totalOpenDisputes"
                    card-title="Open Disputes"
                    card-sub-title="Total number of open disputes"
                    :loading="loading.disputes"
                />
            </v-col>
        </v-row>
        <v-row class="align-center justify-center">
            <v-col cols="11">
                <v-card class="elevation-4">
                    <v-card-title>Daily Totals (Past 30 Days)</v-card-title>
                    <v-skeleton-loader
                        class="mx-auto"
                        type="image"
                        v-if="loading.creditCardTotals"
                    />
                    <v-card-text v-else>
                        <vue-apex-charts
                            height="300"
                            :options="dailyTotalChartOptions"
                            :series="dailyTotalSeries"
                        />
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import accounting from 'accounting';
import axios from 'axios';
import VueApexCharts from 'vue-apexcharts';
import { mapGetters } from 'vuex';

import StatCard from '@/components/dashboard/StatCard';

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'Dashboard',
    components: { StatCard, VueApexCharts },
    data() {
        return {
            alert: false,
            alertMessage: '',
            alertType: 'success',
            loading: {
                stats: false,
                disputes: false,
                creditCardTotals: false,
            },
            stats: {},
            disputes: [],
            quickDateRange: {
                text: 'Year To Date',
                value: 'yearToDate',
            },
            dailyTotalChartOptions: {
                chart: {
                    id: 'daily-total-chart',
                    type: 'line',
                    toolbar: {
                        show: false,
                    },
                },
                xaxis: {
                    categories: [1, 2, 3, 4, 5],
                },
            },
            dailyTotalSeries: [
                {
                    name: 'Credit Volume',
                    data: [0, 0, 0, 0, 0],
                },
            ],
        };
    },
    computed: {
        ...mapGetters([
            'getAppColors',
            'getInternalMxController',
            'getInternalCoreClearController',
            'getQuickDateRangeChoices',
        ]),
        totalVolume() {
            return this.stats.totalVolume
                ? accounting.formatMoney(this.stats.totalVolume.value)
                : '0';
        },
        totalCount() {
            return this.stats.totalCount
                ? accounting.formatNumber(
                      this.stats.totalCount.value,
                      0,
                      '',
                      '.',
                  )
                : '0';
        },
        totalOpenDisputes() {
            if (this.disputes.length === 0) return '0';

            let openDisputes = this.disputes.filter(
                (dispute) =>
                    dispute.itemTypeDescription === 'New Cases' &&
                    (!dispute.resolutionDate ||
                        dispute.resolutionDate === '' ||
                        dispute.resolutionDate === null),
            );

            return openDisputes ? openDisputes.length.toString() : '0';
        },
    },
    mounted() {
        this.initialize();
        this.getDisputes();
        this.getDailyCreditCardTotals();
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        async initialize() {
            if (this.quickDateRange == null) return;

            this.loading.stats = true;

            let dateRange = this.quickDateRange
                ? this.quickDateRange.value
                : '';

            try {
                let response = await axios.get(
                    `${this.getInternalMxController + '/get_stats'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            date_range: dateRange,
                        },
                    },
                );

                this.loading.stats = false;

                if (
                    response.data.data &&
                    response.data.data.aggregations &&
                    response.data.data.aggregations.stats
                ) {
                    this.stats = response.data.data.aggregations.stats;
                } else {
                    console.log('No stats found');
                }
            } catch (error) {
                console.error('Could not get stats');
                console.error(error);

                this.openAlert('error', error.toString());

                this.loading = false;
                this.batches = [];
            }
        },
        async getDisputes() {
            if (this.quickDateRange == null) return;

            this.loading.disputes = true;
            this.alert = false;

            let dateRange = this.quickDateRange
                ? this.quickDateRange.value
                : '';

            try {
                let response = await axios.get(
                    `${this.getInternalMxController + '/get_disputes'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            date_range: dateRange,
                        },
                    },
                );

                this.loading.disputes = false;

                if (response.data.warning) {
                    console.log(response.data.message);
                } else {
                    if (response.data.data.totalRecords > 0) {
                        this.disputes = response.data.data.records;
                    } else {
                        console.log('No disputes found');
                        this.disputes = [];
                    }
                }
            } catch (error) {
                console.error('Could not get list of disputes');
                console.error(error);
            }
        },
        async getDailyCreditCardTotals() {
            this.loading.creditCardTotals = true;
            this.alert = false;

            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/credit_card_payments'
                    }`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {},
                    },
                );

                this.loading.creditCardTotals = false;

                if (response.data.length > 0) {
                    const categories = [];
                    const seriesData = [];

                    response.data.forEach((data) => {
                        categories.push(data.date);
                        seriesData.push(
                            accounting.formatNumber(
                                data.sales_total,
                                2,
                                '',
                                '.',
                            ),
                        );
                    });

                    this.dailyTotalChartOptions = {
                        ...this.dailyTotalChartOptions,
                        ...{
                            xaxis: {
                                categories: categories,
                            },
                        },
                    };

                    this.dailyTotalSeries = [
                        {
                            data: seriesData,
                        },
                    ];
                } else {
                    console.log('No credit card stats found');
                }
            } catch (error) {
                console.error('Could not get credit card stats');
                console.error(error);
            }
        },
    },
};
</script>

<style scoped>
.fit-content {
    width: min-content;
}
>>> .fit-content div.v-select__selection--comma {
    text-overflow: clip;
    max-width: 100%;
    margin: 0;
}
</style>
